:root {
  --mobile-width: 480px;
  --tablet-width: 768px;
  --desktop-width: 1200px;
  --main-font-family: "Montserrat", sans-serif;
  --secondary-font-family: "Seymour One", sans-serif;
  --main-font-style: normal;
  --main-text-color: #131313;
  --inverse-text-color: #fcfcfc;
  --brand-color: #f8d24e;
  --background-color: #84a178;
  --button-text-color: #000;
  --button-text-inverse-color: #fff;
  --button-hover-color: #0a8817;
  --address-text-color: #d6936d;
  --work-time-color: #ababab;
  --copyright-color: #7a7a7a;
  --background-input-color: rgba(248, 210, 78, .1);
  --normal-transition-duration: 250ms;
  --normal-transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

lite-youtube {
  contain: content;
  cursor: pointer;
  max-width: 720px;
  background-color: #000;
  background-position: center;
  background-size: cover;
  display: block;
  position: relative;
}

lite-youtube:before {
  content: "";
  height: 60px;
  width: 100%;
  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAADGCAYAAAAT+OqFAAAAdklEQVQoz42QQQ7AIAgEF/T/D+kbq/RWAlnQyyazA4aoAB4FsBSA/bFjuF1EOL7VbrIrBuusmrt4ZZORfb6ehbWdnRHEIiITaEUKa5EJqUakRSaEYBJSCY2dEstQY7AuxahwXFrvZmWl2rh4JZ07z9dLtesfNj5q0FU3A5ObbwAAAABJRU5ErkJggg==");
  background-position: top;
  background-repeat: repeat-x;
  padding-bottom: 50px;
  transition: all .2s cubic-bezier(0, 0, .2, 1);
  display: block;
  position: absolute;
  top: 0;
}

lite-youtube:after {
  content: "";
  padding-bottom: 56.25%;
  display: block;
}

lite-youtube > iframe {
  width: 100%;
  height: 100%;
  border: 0;
  position: absolute;
  top: 0;
  left: 0;
}

lite-youtube > .lty-playbtn {
  width: 36px;
  height: 36px;
  cursor: pointer;
  z-index: 1;
  filter: grayscale();
  background-color: rgba(0, 0, 0, 0);
  background-image: url("clarity_play-solid.5329a83a.svg");
  border: none;
  transition: filter .1s cubic-bezier(0, 0, .2, 1);
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
}

lite-youtube:hover > .lty-playbtn, lite-youtube .lty-playbtn:focus {
  filter: none;
}

lite-youtube.lyt-activated {
  cursor: pointer;
}

lite-youtube.lyt-activated:before, lite-youtube.lyt-activated > .lty-playbtn {
  opacity: 0;
  pointer-events: none;
}

.lyt-visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  white-space: nowrap;
  width: 1px;
  position: absolute;
  overflow: hidden;
}

